"use client";

import { useQuery, useMutation, useConvexAuth } from "convex/react";
import { api } from "@/convex/_generated/api";
import { Id } from "@/convex/_generated/dataModel";

// Importando componentes do Shadcn/UI que usaremos na página
import { Button } from "@/components/ui/button";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Badge } from "@/components/ui/badge";

// Componente principal que lida com o estado de autenticação
export default function AdminPage() {
  // Hook do Convex para verificar o status da autenticação de forma segura
  const { isLoading, isAuthenticated } = useConvexAuth();

  // 1. Mostra um estado de carregamento enquanto o Clerk verifica o login
  if (isLoading) {
    return <div className="container mx-auto p-4">Verificando autenticação...</div>;
  }

  // 2. Se a verificação terminou e o usuário NÃO está logado, nega o acesso
  if (!isAuthenticated) {
    return (
      <div className="container mx-auto p-4">
        <h1 className="text-2xl font-bold text-red-500">Acesso Negado</h1>
        <p>Você precisa estar logado para acessar esta página.</p>
      </div>
    );
  }

  // 3. Se a verificação terminou e o usuário ESTÁ logado, renderiza o painel
  return <AdminDashboard />;
}

// Componente que busca os dados do admin, só renderiza se autenticado
function AdminDashboard() {
  // Esta query agora só roda quando temos certeza que o usuário está autenticado
  const currentUser = useQuery(api.admin.getCurrentUserWithAdminRole);

  // Mostra um carregamento para a busca dos dados do usuário
  if (currentUser === undefined) {
    return <div className="container mx-auto p-4">Carregando dados do usuário...</div>;
  }

  // Verifica se o usuário autenticado tem o papel de 'admin'
  if (!currentUser.isAdmin) {
    return (
      <div className="container mx-auto p-4">
        <h1 className="text-2xl font-bold text-red-500">Acesso Negado</h1>
        <p>Você não tem permissão de administrador.</p>
      </div>
    );
  }

  // Se for admin, finalmente renderiza o painel de gerenciamento
  return <UserManagementPanel />;
}


// Painel de gerenciamento de usuários (sem alterações)
function UserManagementPanel() {
  const users = useQuery(api.admin.getUsers);
  const terreiros = useQuery(api.admin.getTerreiros);
  const assignUserToTerreiro = useMutation(api.admin.assignUserToTerreiro);
  const updateUserRole = useMutation(api.admin.updateUserRole);

  if (!users || !terreiros) {
    return <div className="container mx-auto p-4">Carregando usuários e terreiros...</div>;
  }

  const getTerreiroName = (terreiroId: Id<"terreiros"> | undefined) => {
    if (!terreiroId) return "Nenhum";
    return terreiros.find((t) => t._id === terreiroId)?.name ?? "Desconhecido";
  };

  return (
    <div className="container mx-auto p-4">
      <h1 className="text-3xl font-bold mb-6">Painel de Administração</h1>
      <h2 className="text-2xl font-semibold mb-4">Gerenciamento de Usuários</h2>
      <div className="border rounded-lg">
        <Table>
          <TableHeader>
            <TableRow>
              <TableHead>Nome</TableHead>
              <TableHead>Email</TableHead>
              <TableHead>Papel (Role)</TableHead>
              <TableHead>Terreiro</TableHead>
              <TableHead className="text-right">Ações</TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {users.map((user) => (
              <TableRow key={user._id}>
                <TableCell>{user.name}</TableCell>
                <TableCell>{user.email}</TableCell>
                <TableCell>
                  <Badge variant={user.role === "admin" ? "default" : "secondary"}>
                    {user.role}
                  </Badge>
                </TableCell>
                <TableCell>{getTerreiroName(user.terreiroId)}</TableCell>
                <TableCell className="text-right space-x-2">
                  <DropdownMenu>
                    <DropdownMenuTrigger asChild>
                      <Button variant="outline" size="sm">Mudar Papel</Button>
                    </DropdownMenuTrigger>
                    <DropdownMenuContent>
                      <DropdownMenuItem
                        onClick={() =>
                          updateUserRole({ userId: user._id, role: "admin" })
                        }
                      >
                        Tornar Admin
                      </DropdownMenuItem>
                      <DropdownMenuItem
                        onClick={() =>
                          updateUserRole({ userId: user._id, role: "member" })
                        }
                      >
                        Tornar Membro
                      </DropdownMenuItem>
                    </DropdownMenuContent>
                  </DropdownMenu>

                  <Select
                    onValueChange={(terreiroId) => {
                      assignUserToTerreiro({
                        userId: user._id,
                        terreiroId: terreiroId as Id<"terreiros">,
                      });
                    }}
                    defaultValue={user.terreiroId}
                  >
                    <SelectTrigger className="w-[180px] inline-flex">
                      <SelectValue placeholder="Associar Terreiro" />
                    </SelectTrigger>
                    <SelectContent>
                      {terreiros.map((terreiro) => (
                        <SelectItem key={terreiro._id} value={terreiro._id}>
                          {terreiro.name}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </div>
    </div>
  );
}
